<!--网点商品管理-->
<template>
  <div class="content">
    <el-menu :default-active="activeIndex" :style="{ height: '60px'}" mode="horizontal" @select="handleSelect">
      <el-menu-item index="1" style="margin: 0 10px">
        <span>在用礼品</span>
      </el-menu-item>
      <el-menu-item v-show="false" index="2" style="margin: 0 10px">
        <span>历史礼品</span>
      </el-menu-item>
      <div style="float: right;margin: 10px 20px 30px 0">
        <el-button type="primary" @click="new_shop()">添加礼品</el-button>
      </div>
    </el-menu>
    <!--  页面表格 start -->
    <el-card style="width: 100%">
      <el-table
        class="account-plat-fixed-right account-plat-fixed "
        border
        ref="tableData"
        :data="tableData"
        tooltip-effect="dark"
        @sort-change="sortColumn"
        :row-style="{'height':'10px'}"
        :cell-style="{'padding':'5px 0px'}"
        :header-cell-style="{'background': '#4B86BC','color':'#ffffff','borderRight': '1px solid #E3E5EB' ,'padding':'0px','height':'45px','line-height':'45px' }"
      >
        <el-table-column
          fixed
          align="center"
          type="index"
          label="序号"
          width="50">
        </el-table-column>
        <el-table-column
          prop="mbgi_goods_name"
          label="商品名称"
          align="center"
          show-overflow-tooltip
        >
        </el-table-column>
        <el-table-column
          prop="mbgi_goods_img_path"
          label="商品图片"
          align="center"
          show-overflow-tooltip
        >
          <template slot-scope="scope">
            <el-image style="width: 80px;height: 60px;" :src="scope.row.mbgi_goods_img_path"/>
          </template>
        </el-table-column>
        <el-table-column
          prop="mbgi_goods_marker"
          label="品牌"
          align="center"
          show-overflow-tooltip
        >
        </el-table-column>
        <el-table-column
          prop="mbgi_goods_describer"
          label="规格"
          align="center"
          show-overflow-tooltip
        >
        </el-table-column>
        <el-table-column
          prop="mbgi_money_1"
          label="原价"
          align="center"
          show-overflow-tooltip
        >
        </el-table-column>
        <el-table-column
          fixed="right"
          label="操作"
          min-width="100"
          align="center"
        >
          <template slot-scope="scope" v-if="scope.row.lai_detail_status !=='4'">
          <el-button @click.native.stop="update_commodity(scope.$index,scope.row)" type="text" size="small">编辑</el-button>
          <el-button v-show="false" @click.native.stop="off_the_shelf(scope.$index,scope.row)" type="text" size="small">放入历史</el-button>
            <!-- <el-tooltip content="编辑" placement="top">
              <el-button
                type="primary" icon="el-icon-edit-outline" circle
                size="mini"
                @click.native.stop="update_commodity(scope.$index,scope.row)"></el-button>
            </el-tooltip>
            <el-tooltip content="放入历史" placement="top" v-show="false">
              <el-button
                type="danger" icon="el-icon-document-delete" circle
                size="mini"
                @click.native.stop="off_the_shelf(scope.$index,scope.row)"></el-button>
            </el-tooltip> -->
          </template>
        </el-table-column>
      </el-table>
      <!--  页面表格 end   -->
      <!--page start-->
      <el-pagination
        style="margin-top: 20px"
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryParam.currentPage"
        :page-sizes="[10, 20, 50, 100,200]"
        :page-size="queryParam.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="queryParam.totalRow">
      </el-pagination>
      <!--page end-->
    </el-card>

    <el-dialog
      :show-close="false"
      :close-on-press-escape="false"
      :close-on-click-modal="false"
      :visible.sync="dialogVisible"
      width="60%"
      class="account-plat-dialog"
    >
      <div slot="title" style="text-align: left;font-size:20px;font-weight: bold;">
        <span>{{ this.tableDialgTitle }}</span>
      </div>
      <el-form class="account-plat-formitem" :model="commodityForm" ref="commodityForm" label-width="120px"
        style="position: relative" :rules="commodityFormrules">
        <div style="width: 50%;">
          <el-form-item label="商品名称:" prop="mbgi_goods_name">
            <el-input placeholder="请输入商品名称" :maxlength="20" v-model="commodityForm.mbgi_goods_name"
            ></el-input>
          </el-form-item>
          <el-form-item label="商品简称：" prop="mbgi_goods_alias_name"style="margin-top: 20px;">
            <el-input placeholder="请输入商品简称" :maxlength="6" v-model="commodityForm.mbgi_goods_alias_name"
              :rules="commodityFormrules.mbgi_money_2" ></el-input>
          </el-form-item>
          <el-form-item label="品牌:" prop="mbgi_goods_marker"style="margin-top: 20px;">
            <el-input placeholder="请输入品牌" :maxlength="6" v-model="commodityForm.mbgi_goods_marker"
              :rules="commodityFormrules.mbgi_goods_marker"></el-input>
          </el-form-item>
          <el-form-item label="规格:" prop="mbgi_goods_describer" style="margin-top: 20px;">
            <el-input placeholder="请输入规格" :maxlength="6" v-model="commodityForm.mbgi_goods_describer"
              :rules="commodityFormrules.mbgi_goods_describer"></el-input>
          </el-form-item>
          <el-form-item label="原价:" prop="mbgi_money_1" style="margin-top: 20px;position: relative;">
            <el-input @change="fixedChange" placeholder="请输入商品价格" :maxlength="6" v-model="commodityForm.mbgi_money_1"
              :rules="commodityFormrules.mbgi_money_1" ref="FormDate"></el-input>
              <div style="position: absolute; top: 0px; right: -20px;">元</div>
          </el-form-item>
        </div>
      
        <div style="position: absolute;top: 20px;right: 13%;">
          <el-image style="width: 200px;height: 200px;" :src="commodityForm.mbgi_goods_img_path1" />
          <el-button @click="onOpen()" style="position: absolute;top: 80px;right: 50px">上传图片</el-button>
          <div style="text-align: center;margin-top: 20px">商品图片</div>
        </div>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancelForm('commodityForm')">取 消</el-button>
        <el-button type="primary" @click="submitForm('commodityForm')">确 定</el-button>
      </div>
      </el-dialog>

    <!--截图功能-->
    <el-dialog :title="options.title" :visible.sync="img_dialogVisible" :before-close="handleClose">
      <div class="el-cropper">
        <div>
          <div style="margin: 10px 0">
            <div style="font-weight: 600">操作说明：</div>
            <div>通过鼠标滚轮调整图片大小</div>
            <div>通过鼠标左键调整裁剪框位置</div>
          </div>
          <div class="cropper-content">
            <div class="cropper">
              <vueCropper ref="cropper"
                          :img="options.img"
                          :outputSize="options.outputSize"
                          :outputType="options.outputType"
                          :full="options.full"
                          :canMove="options.canMove"
                          :canMoveBox="options.canMoveBox"
                          :original="options.original"
                          :autoCrop="options.autoCrop"
                          :autoCropWidth="options.autoCropWidth"
                          :autoCropHeight="options.autoCropHeight"
                          :fixedBox="options.fixedBox"
                          :enlarge="options.enlarge"
                          :centerBox="options.centerBox"
                          :fixedNumber="options.fixedNumber"
                          :fixed="options.fixed"
                          :maxImgSize="options.maxImgSize"
                          :mode="options.mode"
                          :infoTrue="options.infoTrue"
                          :high="options.high"
                          @realTime="realTime"
              ></vueCropper>
            </div>
            <div class="show-preview" :style="{'overflow': 'hidden'}" v-show="false">
              <div class="wait">预览区域：</div>
              <div class="preview">
                <img :src="imgsrc" alt="" :style="scaleFun">
              </div>
              <!--              <div class="detailsclass">-->
              <!--                <p class="">截图详情：</p>-->
              <!--                <p v-text="options.details" :title="options.details"></p>-->
              <!--              </div>-->
            </div>
          </div>
        </div>
        <div style="  margin-top: 10px;">
          <el-row>
            <!-- 选择图片 -->
            <el-col :span="6">
              <div>
                <label class="el-button el-button--default" for="uploads2">选择图片
                  <input type="file" id="uploads2" ref="reference" style="position:absolute; clip:rect(0 0 0 0);"
                         accept="image/png, image/jpeg, image/jpg" @change="uploadImg($event, 1)"/>
                </label>
              </div>
            </el-col>
            <!-- 功能按钮 -->
            <el-col :span="6">
              <div>
                <el-button @click="rotateLeft"><span style="font-weight: 600;">↺</span></el-button>
                <el-button @click="rotateRight"><span style="font-weight: 600;">↻</span></el-button>
              </div>
            </el-col>
            <el-col :span="12">
              <div>
                <el-button @click="img_dialogVisible = false">取 消</el-button>
                <el-button @click="oncropped('base64')">确 定</el-button>
              </div>
            </el-col>
          </el-row>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {VueCropper} from 'vue-cropper';
import axios from "axios";

export default {
  name: "OutletGoods",
  components: {
    VueCropper,
  },
  data() {
    var mbgi_money_1 = (rule, value, callback) => {
      let reg = /^(\d+)(\.\d+)?$/;
      reg.test(value*1)
      if (!reg.test(value*1)) {
        callback(new Error('请输入商品原价'));
      } else {
        callback();
      }
    };
    return {
      activeIndex: '1',
      queryParam: {
        totalRow: 0,
        currentPage: 1,
        pageSize: this.customGlobal.pageSize,
      },
      key: '',//标签页
      mbmi_status: '',
      tableData: [],//表格数据
      sortOrderCol: '',
      dialogVisible: false,
      tableDialgTitle: '',
      commodityForm: {
        flag: '',
        mbgi_goods_name: '',
        mbgi_goods_alias_name: '',
        mbgi_goods_describer: '',
        mbgi_goods_img_path1: '',
        mbgi_goods_marker: '',
        mbgi_money_1: '',
      },
       //表单校验
       commodityFormrules: {
        mbgi_goods_name: [
            { pattern:/^[\u4E00-\u9FA5A-Za-z0-9]*$/, message: '商户名称不可输入空格、特殊符号', trigger: 'blur' },
            { required: true, message: '请输入商户名称', trigger: 'blur' },
          ],
          mbgi_goods_alias_name: [
            { pattern:/^[\u4E00-\u9FA5A-Za-z0-9]*$/, message: '商户简称不可输入空格、特殊符号', trigger: 'blur' },
            { required: true, message: '请输入商户简称', trigger: 'blur' },
          ],
          mbgi_goods_marker: [
            { pattern:/^[\u4E00-\u9FA5A-Za-z0-9]*$/, message: '请输入商品品牌', trigger: 'blur' },
            { required: true, message: '请输入商品品牌', trigger: 'blur' },
          ],
          mbgi_goods_describer: [
            { pattern:/^[\u4E00-\u9FA5A-Za-z0-9]*$/, message: '请输入商品规格', trigger: 'blur' },
            { required: true, message: '请输入商品规格', trigger: 'blur' },
          ],
          mbgi_money_1: [
            { required: true, message: '请输入商品原价', trigger: 'blur' },
            { validator:mbgi_money_1, trigger: 'blur' },
          ],
        },
      tableData_bank: [],
      mbgi_mer_uuid: '',
      //截图功能
      options: {
        img: "",
        outputSize: 1,//	 裁剪生成图片的质量 0.1 - 1
        outputType: 'png | jpeg | jpg',	//裁剪生成图片的格式 jpeg || png || webp
        full: true,//是否输出原图比例的截图,true是不失真
        canMove: true,//图片是否可以移动
        canMoveBox: true,//	截图框能否拖动
        original: false,//图片按照原始比例渲染,true会超出屏幕区域，false是适应屏幕区域
        autoCrop: true,//是否默认生成截图框
        autoCropWidth: 250,
        autoCropHeight: 250,
        fixedBox: true,//固定截图框大小 不允许改变
        enlarge: 1,
        centerBox: true,//截图框是否被限制在图片里面
        fixed: true,//	是否开启截图框宽高固定比例
        fixedNumber: [1, 1],// 截图框的宽高比例 [宽度, 高度]
        maxImgSize: 2000,
        mode: 'contain',//整个图片填充裁剪框 contain , cover, 100px, 100% auto
        high: true,
        infoTrue: true,//true截图框展示真实图片的宽高,false截图框内显示可缩放图片

        title: '图像裁剪',
        details: '选择图片，上传',
      },
      imgsrc: '',
      img_dialogVisible: false,
      mbgi_uuid: '',
    }
  },
  created() {
    this.select_table();
  },
  // 预览实时缩放
  computed: {
    scaleFun: function () {
      var scale = this.scaleratioXY;
      return `transform:scale(${scale},${scale})`;
    }
  },
  methods: {
    handleSelect(key, keyPath) {
      console.log('上：' + key, keyPath);
      this.queryParam.currentPage = 1;
      this.key = key;
      // if (this.key === '1') {
      //   this.mbmi_status = '2';
      // } else if (this.key === '2') {
      //   this.mbmi_status = '1';
      // }
      // this.select_table();
    },
    /**  表格列排序设置   */
    sortColumn(column) {
      //  console.log("已经排序项：" + JSON.stringify(column));
      let orderFlag = '';
      if (column.order != null) {
        if (column.order === "descending") {
          orderFlag = 'desc';
        } else if (column.order === "ascending") {
          orderFlag = 'asc';
        }
        //  console.info("属性名称："+ column.prop+",排序方式："+column.order);
        this.sortOrderCol = column.prop + " " + orderFlag;
        //  console.info("用户选择排序列："+this.sortOrderCol);

        this.select_gift();
      } else {
        this.sortOrderCol = '';
      }
    },
    fixedChange() {
      if (this.commodityForm.mbgi_money_1=='') return this.commodityForm.mbgi_money_1 = ''
      if(typeof (this.commodityForm.mbgi_money_1*1) === 'number' && !isNaN(this.commodityForm.mbgi_money_1*1)){
        this.commodityForm.mbgi_money_1 = (this.commodityForm.mbgi_money_1*1).toFixed(2)
      }
    },
    /**----分页方法-------start----*/
    handleSizeChange(val) {
      //  console.log(`每页 ${val} 条`);
      this.queryParam.pageSize = val;
      this.select_gift();
    },
    handleCurrentChange(val) {
      //  console.log(`当前页: ${val}`);
      this.queryParam.currentPage = val;
      this.select_gift();
    },
    handleClose(done) {
      this.$confirm('确认关闭？')
        .then(_ => {
          done();
          this.onclose();
        })
        .catch(_ => {
        });
    },
    // 预览
    realTime(data) {
      var that = this;
      var dataoption = data;
      this.$refs.cropper.getCropData(data => {
        that.imgsrc = data;
        if (dataoption.w >= 200 || dataoption.h >= 200) {
          if (dataoption.w >= dataoption.h) {
            that.scaleratioXY = 200 / dataoption.w;
          }
          if (dataoption.h > dataoption.w) {
            that.scaleratioXY = 200 / dataoption.h;
          }
        }
      });
    },
    // 选择要上传的图片
    uploadImg(e, num) {
      console.log('--s');
      // 选择要上传的图片
      // this.option.img
      var that = this;
      var file = e.target.files[0];


      if (!/\.(jpg|jpeg|png|JPG|PNG)$/.test(e.target.value)) {
        alert('图片类型必须是.gif,jpeg,jpg,png中的一种');
        return false;
      }
      var reader = new FileReader();
      console.log('--s1');
      reader.onload = e => {
        console.log('--s2');
        let data;
        if (typeof e.target.result === 'object') {
          // 把Array Buffer转化为blob 如果是base64不需要
          data = window.URL.createObjectURL(new Blob([e.target.result]));
        } else {
          data = e.target.result;
        }
        if (num === 1) {
          that.options.img = data;
          // var dat = {img: data};
          // that.$emit('updateimg', dat);
        } else if (num === 2) {
          this.example2.img = data;
        }
      };
      // 转化为base64
      reader.readAsDataURL(file);
      console.log('--s3');
      // 转化为blob
      // reader.readAsArrayBuffer(file)
    },
    // 图片缩放
    changeScale(num) {
      num = num || 1;
      this.$refs.cropper.changeScale(num);
    },
    // 左旋转
    rotateLeft() {
      this.$refs.cropper.rotateLeft();
    },
    // 右旋转
    rotateRight() {
      this.$refs.cropper.rotateRight();
    },
    // 裁剪
    oncropped(type) {
      this.$refs.reference.value=''
      var that = this;
      if (type === 'blob') {
        this.$refs.cropper.getCropBlob(data => {
          // var img = window.URL.createObjectURL(data);
          // console.info("图片数据2："+data);
        });
      } else {
        this.$refs.cropper.getCropData(data => {
          var dats = {img: data};
          // console.info("图片数据1："+data);
          // that.$emit('getcropperimg', dats);
          if (this.options.img === '') {
            alert('您还没有选择图片');
          } else {
            //调用上传接口
            // this.onclose();
            this.uploadFileToServer(data);
            this.img_dialogVisible = false;
            this.options.img = '';
            this.imgsrc = '';
          }
        });
      }
    },
    //打开
    onOpen() {
      this.img_dialogVisible = true;

    },
    // base64转成bolb对象
    dataURItoBlob(base64Data) {
      let byteString;
      if (base64Data.split(',')[0].indexOf('base64') >= 0) {
        byteString = atob(base64Data.split(',')[1]);
      } else {
        byteString = unescape(base64Data.split(',')[1]);
      }
      let mimeString = base64Data.split(',')[0].split(':')[1].split(';')[0];
      let ia = new Uint8Array(byteString.length);
      for (let i = 0; i < byteString.length; i++) {
        ia[i] = byteString.charCodeAt(i);
      }
      return new Blob([ia], {type: mimeString});
    },
    async uploadFileToServer(data) {
      let img = new Image();
      img.src = data;
      img.onload = async () => {
        // let _data = this.compress(img);
        let blob = this.dataURItoBlob(data);
        let formData = new FormData();
        // formData.append('file', blob, this.options.fileInfo.name);
        formData.append('file', blob, this.customGlobal.getFormatDateTimeString("3") + ".png");
        this.uploadFileRequest(formData);
      };
    },
    async uploadFileRequest(params) {
      let that = this;
      const res = axios({
        method: 'post',
        url: this.customGlobal.serverUrl.orgBaseURI + "/fr/postCallPowerFileUpload?userid=" + this.customGlobal.userInfoExt.cui_login_id + "&logic=2",
        data: params,
        headers: {
          'Content-Type': 'multipart/form-data'
        },
        onUploadProgress: function (progressEvent) {
          console.info("progressEvent:" + JSON.stringify(progressEvent));
          // 对原生进度事件的处理
        },

      }).then(function (response) {
        console.info("response:" + JSON.stringify(response));
        if (response.data.status === 'success') {
          console.info("上传成功：" + response.data.rawfilename);
          // let img = "http://192.168.3.62:38080/fr/imgserver/" + response.data.rawfilename;
          let img = that.customGlobal.serverUrl.orgBaseURI + "/fr/imgserver/" + response.data.rawfilename;
          console.info("图片：" + img);
          that.commodityForm.mbgi_goods_img_path1 = img;
          console.info("图片：" + that.commodityForm.mbgi_goods_img_path1);
        } else {
          console.info("上传失败");
        }
      }).catch(function (error) {
        console.log(error);
      });
    },
    //添加礼品
    new_shop() {
      this.dialogVisible = true;
      this.tableDialgTitle = '添加商品';
      this.commodityForm.flag = '1';
      this.commodityForm.mbgi_goods_name = '';
      this.commodityForm.mbgi_goods_alias_name = '';
      this.commodityForm.mbgi_goods_img_path1 = '';
      this.commodityForm.mbgi_goods_describer = '';
      this.commodityForm.mbgi_goods_marker = '';
      this.commodityForm.mbgi_money_1 = '';
    },
    //编辑礼品
    update_commodity(index, row) {
      this.dialogVisible = true;
      this.tableDialgTitle = '编辑商品';
      this.commodityForm.flag = '2';
      this.mbgi_uuid = row.mbgi_uuid;
      this.commodityForm.mbgi_goods_name = row.mbgi_goods_name;
      this.commodityForm.mbgi_goods_alias_name = row.mbgi_goods_alias_name;
      this.commodityForm.mbgi_goods_img_path1 = row.mbgi_goods_img_path;
      this.commodityForm.mbgi_goods_describer = row.mbgi_goods_describer;
      this.commodityForm.mbgi_goods_marker = row.mbgi_goods_marker;
      this.commodityForm.mbgi_money_1 = row.mbgi_money_1;
    },
    //关闭
    cancelForm(formName) {
      this.$refs.commodityForm.resetFields();
      this.dialogVisible = false;
    },
    //保存
    submitForm() {
      console.log('-------------------------------');
      this.$refs.commodityForm.validate((valid) => {
        
      console.log(valid);
        if (valid) {
          let that = this;
          let _jsondata_ = {
            flag: that.commodityForm.flag,
            mbgi_mer_uuid: that.mbgi_mer_uuid,
            mbgi_goods_name: that.commodityForm.mbgi_goods_name,
            mbgi_goods_alias_name: that.commodityForm.mbgi_goods_alias_name,
            mbgi_goods_img_path: that.commodityForm.mbgi_goods_img_path1,
            mbgi_goods_describer: that.commodityForm.mbgi_goods_describer,
            mbgi_goods_marker: that.commodityForm.mbgi_goods_marker,
            mbgi_money_1: that.commodityForm.mbgi_money_1,
            mbgi_cui_id: that.customGlobal.userInfoExt.cui_uuid,
            mbgi_bank_center_id: that.customGlobal.userInfoExt.cui_bank_center_id,
            mbgi_province_id: that.customGlobal.userInfoExt.cui_province_id,
            mbgi_city_id: that.customGlobal.userInfoExt.cui_city_id,
            mbgi_county_id: that.customGlobal.userInfoExt.cui_county_id,
            mbgi_station_id: that.customGlobal.userInfoExt.cui_station_id,
            mbgi_uuid: that.mbgi_uuid,
          };
          const loading = this.$loading({
            lock: true,
            text: '提交中...',
            target: '.main',
            spinner: 'el-icon-loading',

            background: 'rgba(0, 0, 0, 0)'
          });
          var a1 = this.customGlobal.postHttpClient(
            this.customGlobal.serverUrl.commodity_preservation,
            //this.customGlobal.serverUrl.baseURI,
            this.customGlobal.serverUrl.orgBaseURI,
            { "Content-Type": "application/x-www-form-urlencoded" },
            this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
          );
          a1.then((res) => {
            if (res.data.status == 'success') {
              loading.close();
              this.$message({
                type: 'success',
                showClose: true,
                duration: 3000,
                dangerouslyUseHTMLString: true,
                message: '<span style="color:black;">商品保存成功</span>'
              });

              this.$refs.commodityForm.resetFields();
              that.dialogVisible = false;
              that.queryParam.currentPage = 1;
              that.select_gift();
            } else {
              console.info("商品保存失败");
              loading.close();
              if (res.data.msg === '请登录认证') {
                this.$router.push({ path: '/login' });
              }
            }
          }, error => {
            loading.close();
            // this.companyDialogFormVisible =false;//保存失败关闭dialog
            this.$alert(JSON.stringify(error.message), '商品保存出错!', {
              confirmButtonText: '确定',
              callback: action => {

              }
            });
          }).catch((res) => {
            console.log("异步调用失败返回:" + JSON.stringify(res));
            loading.close();
            // this.companyDialogFormVisible =false;//保存失败关闭dialog
            this.$alert(JSON.stringify(res.message), '商品保存出错', {
              confirmButtonText: '确定',
              callback: action => {

              }
            });
          });
        }else{
          this.$message({
          message: '请将信息填写完整！',
          type: 'warning'
        });
        }
      });
    },
    //礼品查询
    select_gift() {
      let that = this;
      let _jsondata_ = {
        mbgi_mer_uuid: that.mbgi_mer_uuid,
        flag: '4',
      };
      const loading = this.$loading({
        lock: true,
        text: '提交中...',
        target: '.main',
        spinner: 'el-icon-loading',

        background: 'rgba(0, 0, 0, 0)'
      });
      var a1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.commodity_preservation,
        //this.customGlobal.serverUrl.baseURI,
        this.customGlobal.serverUrl.orgBaseURI,
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
      );
      a1.then((res) => {
        if (res.data.status === 'success') {
          loading.close();
          that.tableData = res.data.rows;
          that.queryParam.totalRow = res.data.total;
        } else {
          console.info("商品查询失败");
          loading.close();
          if (res.data.msg === '请登录认证') {
            this.$router.push({path: '/login'});
          }
        }
      }, error => {
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(error.message), '商品查询失败!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
        console.log("异步调用失败返回:" + JSON.stringify(res));
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(res.message), '商品查询失败', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });
    },
    //网点查询
    select_table() {
      let that = this;
      let _jsondata_ = {
        mbmi_bank_center_id: that.customGlobal.userInfoExt.cui_bank_center_id,
        mbmi_province_id: that.customGlobal.userInfoExt.cui_province_id,
        mbmi_city_id: that.customGlobal.userInfoExt.cui_city_id,
        mbmi_county_id: that.customGlobal.userInfoExt.cui_county_id,
        mbmi_station_id: that.customGlobal.userInfoExt.cui_station_id,
        mbmi_flag: '1',
        mbmi_mer_name: '',
        mbmi_status: '2',
        perPageSize: 100,
        currentPage: 1,
      };
      const loading = this.$loading({
        lock: true,
        text: '提交中...',
        target: '.main',
        spinner: 'el-icon-loading',

        background: 'rgba(0, 0, 0, 0)'
      });
      var a1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.mobileBankingMerchant_select,
        //this.customGlobal.serverUrl.baseURI,
        this.customGlobal.serverUrl.orgBaseURI,
        {"Content-Type": "application/x-www-form-urlencoded"},
        this.qs.stringify({jsondata: JSON.stringify(_jsondata_)})
      );
      a1.then((res) => {
        if (res.data.status === 'success') {
          loading.close();
          that.tableData_bank = res.data.rows;
          if (that.tableData_bank.length !== 0) {
            that.mbgi_mer_uuid = that.tableData_bank[0].mbmi_uuid;
            that.queryParam.currentPage = 1;
            that.select_gift();
          }
        } else {
          console.info("网点查询失败");
          loading.close();
          if (res.data.msg === '请登录认证') {
            this.$router.push({path: '/login'});
          }
        }
      }, error => {
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(error.message), '网点查询失败!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
        console.log("异步调用失败返回:" + JSON.stringify(res));
        loading.close();
        // this.companyDialogFormVisible =false;//保存失败关闭dialog
        this.$alert(JSON.stringify(res.message), '网点查询失败', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });
    },
  },
}
</script>

<style scoped>
.account-plat-fixed-right /deep/ .el-table__fixed-right {
  height: 100% !important;
}

.account-plat-fixed /deep/ .el-table__fixed {
  height: 100% !important;
}

.content >>> .el-table::before {
  content: '';
  position: absolute;
  background-color: transparent;
  z-index: 1;
}

.account-plat-dialog /deep/ .el-dialog__body {
  padding: 0px 20px !important;
  color: #606266 !important;
  font-size: 14px !important;
