  margin-left: 0px !important;
}

.account-plat-formitem /deep/ .el-form-item {
  margin-bottom: 3px !important;
}

/*标题*/
.el-cropper .tit {
  text-align: left;
  height: 42px;
  /*padding: 5px 0px;*/
  line-height: 40px;
  background-color: #fff;
  border-bottom: 1px solid lightgray
}

/*功能btn*/
.el-cropper .l-btn {
  display: inline-block;
