}

/*中盒*/
.el-cropper .cropper-content {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-around;
  overflow: hidden;
}

/*详情*/
.el-cropper .detailsclass p {
  text-align: left;
  font-family: "Arial", "Microsoft YaHei", "黑体", "宋体", sans-serif;
}

/**/
.el-cropper .detailsclass p:first-child {
  text-align: left;
  font-weight: 400;
  color: black;
}

/**/
