  width: 200px;
  margin-top: 30px;
}

.el-cropper .preview {
  border: 1px solid #ccc;
  overflow: hidden;
}

.show-preview img {
  transform-origin: 0 0 0
}

.wait {
  width: 200px;
  padding: 0px 0px 15px 0px;
  font-weight: 400;
  color: black;
}

.preview {
  width: 200px;
  height: 200px;
  background: #ddd;
}

.account-plat-dialog /deep/ .el-dialog__body {
  padding: 0px 20px !important;
  color: #606266 !important;
  font-size: 14px !important;
  word-break: break-all !important;
  margin-left: 0px !important;
}

.row {
  display: flex;
  align-items: center;
  width: 100%;
  flex-wrap: wrap;
}

.item {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 10%;
  padding: 10px;
  /*padding-top: 10px;*/
  /*padding-bottom: 10px;*/
}

.el-tag:hover {
  cursor: pointer;
}
</style>



